'use client'

import { useState } from 'react'

const STOPS_BY_LINE: Record<string, string[]> = {
  '30': ['Agdal', 'Centre-Ville', 'Hay Riad', 'Témara'],
  '15': ['Centre-Ville', 'Hassan II', 'Hay Riad', 'Extension'],
  '42': ['Casa Port', 'Derb Sultan', 'Maarif', 'Ain Diab'],
  '2': ['Gare Centrale', 'Boulevard Zerktouni', 'Ben M\'Sick'],
  '8': ['Medina', 'Place Jemaâ el-Fnaa', 'Nouvelle Médina', 'Hivernage'],
  '5': ['Port', 'Grand Socco', 'Petit Socco', 'Kasbah'],
  '12': ['Medina Fès', 'Borj Nord', 'Place de la Résistance', 'Nouvelle Ville']
}

interface JourneyConfigurationProps {
  city: string
  line: {
    number: string
    terminus_a: string
    terminus_b: string
  }
  onSubmit: (details: any) => void
  onBack: () => void
}

export default function JourneyConfiguration({
  city,
  line,
  onSubmit,
  onBack
}: JourneyConfigurationProps) {
  const [departure, setDeparture] = useState('')
  const [arrival, setArrival] = useState('')
  const [date, setDate] = useState('')

  const stops = STOPS_BY_LINE[line.number] || []
  const departureIndex = stops.indexOf(departure)
  const arrivalIndex = stops.indexOf(arrival)

  // Filter arrival stops to only show those after departure
  const availableArrivals = departure
    ? stops.slice(stops.indexOf(departure) + 1)
    : stops

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (departure && arrival && date) {
      onSubmit({ departure, arrival, date })
    }
  }

  const isFormValid = departure && arrival && date

  return (
    <div className="ml-[280px] min-h-screen bg-white p-8">
      <div className="max-w-2xl mx-auto">
        {/* Breadcrumb */}
        <div className="mb-8 text-sm text-[#374151]">
          <span>{city}</span>
          <span className="mx-2">›</span>
          <span>Ligne {line.number}</span>
          <span className="mx-2">›</span>
          <span className="text-[#1e40af] font-medium">Détails du trajet</span>
        </div>

        {/* Header */}
        <div className="mb-8 flex items-center justify-between">
          <div>
            <h2 className="text-3xl font-bold text-[#1e40af] mb-2">
              Configuration du trajet
            </h2>
            <p className="text-[#374151]">
              Sens : {line.terminus_a} → {line.terminus_b}
            </p>
          </div>
          <button
            onClick={onBack}
            className="px-6 py-2 text-[#1e40af] font-medium hover:bg-gray-100 rounded-lg transition-colors"
          >
            Retour
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="space-y-6 bg-white border border-[#e5e7eb] rounded-lg p-6">
          {/* Gare de départ */}
          <div>
            <label className="block text-[#374151] font-medium mb-2">
              Gare de départ
            </label>
            <select
              value={departure}
              onChange={(e) => {
                setDeparture(e.target.value)
                setArrival('') // Reset arrival when departure changes
              }}
              className="w-full px-4 py-3 border border-[#e5e7eb] rounded-lg text-[#374151] focus:outline-none focus:ring-2 focus:ring-[#1e40af] focus:border-transparent"
              required
            >
              <option value="">Sélectionnez une gare</option>
              {stops.map((stop) => (
                <option key={stop} value={stop}>
                  {stop}
                </option>
              ))}
            </select>
          </div>

          {/* Gare d'arrivée */}
          <div>
            <label className="block text-[#374151] font-medium mb-2">
              Gare d'arrivée
            </label>
            <select
              value={arrival}
              onChange={(e) => setArrival(e.target.value)}
              disabled={!departure}
              className="w-full px-4 py-3 border border-[#e5e7eb] rounded-lg text-[#374151] focus:outline-none focus:ring-2 focus:ring-[#1e40af] focus:border-transparent disabled:opacity-50 disabled:cursor-not-allowed"
              required
            >
              <option value="">Sélectionnez une gare</option>
              {availableArrivals.map((stop) => (
                <option key={stop} value={stop}>
                  {stop}
                </option>
              ))}
            </select>
          </div>

          {/* Date */}
          <div>
            <label className="block text-[#374151] font-medium mb-2">
              Date
            </label>
            <input
              type="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              className="w-full px-4 py-3 border border-[#e5e7eb] rounded-lg text-[#374151] focus:outline-none focus:ring-2 focus:ring-[#1e40af] focus:border-transparent"
              required
            />
          </div>

          {/* Submit Button */}
          <button
            type="submit"
            disabled={!isFormValid}
            className="w-full bg-[#1e40af] text-white py-3 rounded-lg font-bold hover:bg-[#1e3a8a] transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
            Rechercher les horaires
          </button>
        </form>
      </div>
    </div>
  )
}
