'use client'

interface Line {
  number: string
  terminus_a: string
  terminus_b: string
  stops: string[]
}

const LINES_BY_CITY: Record<string, Line[]> = {
  'Rabat': [
    {
      number: '30',
      terminus_a: 'Agdal',
      terminus_b: 'Témara',
      stops: ['Agdal', 'Centre-Ville', 'Hay Riad', 'Témara']
    },
    {
      number: '15',
      terminus_a: 'Centre-Ville',
      terminus_b: 'Hay Riad',
      stops: ['Centre-Ville', 'Hassan II', 'Hay Riad', 'Extension']
    }
  ],
  'Casablanca': [
    {
      number: '42',
      terminus_a: 'Casa Port',
      terminus_b: 'Ain Diab',
      stops: ['Casa Port', 'Derb Sultan', 'Maarif', 'Ain Diab']
    },
    {
      number: '2',
      terminus_a: 'Gare Centrale',
      terminus_b: 'Ben M\'Sick',
      stops: ['Gare Centrale', 'Boulevard Zerktouni', 'Ben M\'Sick']
    }
  ],
  'Marrakech': [
    {
      number: '8',
      terminus_a: 'Medina',
      terminus_b: 'Hivernage',
      stops: ['Medina', 'Place Jemaâ el-Fnaa', 'Nouvelle Médina', 'Hivernage']
    }
  ],
  'Tanger': [
    {
      number: '5',
      terminus_a: 'Port',
      terminus_b: 'Kasbah',
      stops: ['Port', 'Grand Socco', 'Petit Socco', 'Kasbah']
    }
  ],
  'Fès': [
    {
      number: '12',
      terminus_a: 'Medina Fès',
      terminus_b: 'Nouvelle Ville',
      stops: ['Medina Fès', 'Borj Nord', 'Place de la Résistance', 'Nouvelle Ville']
    }
  ]
}

interface LineDisplayProps {
  city: string
  onLineSelect: (line: Line) => void
  onBack: () => void
}

export default function LineDisplay({ city, onLineSelect, onBack }: LineDisplayProps) {
  const lines = LINES_BY_CITY[city] || []

  return (
    <div className="ml-[280px] min-h-screen bg-white p-8">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="mb-8 flex items-center justify-between">
          <div>
            <h2 className="text-3xl font-bold text-[#1e40af] mb-2">
              Lignes disponibles — {city}
            </h2>
            <p className="text-[#374151]">Sélectionnez une ligne pour continuer</p>
          </div>
          <button
            onClick={onBack}
            className="px-6 py-2 text-[#1e40af] font-medium hover:bg-gray-100 rounded-lg transition-colors"
          >
            Retour
          </button>
        </div>

        {/* Lines Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {lines.map((line) => (
            <button
              key={line.number}
              onClick={() => onLineSelect(line)}
              className="bg-white border border-[#e5e7eb] rounded-lg p-6 text-left hover:shadow-lg hover:border-[#1e40af] transition-all cursor-pointer group"
            >
              {/* Line Badge */}
              <div className="inline-block bg-[#1e40af] text-white px-3 py-1 rounded mb-4 font-bold text-sm">
                Ligne {line.number}
              </div>

              {/* Route */}
              <div className="mb-4">
                <p className="text-[#374151] font-medium">
                  {line.terminus_a} ↔ {line.terminus_b}
                </p>
              </div>

              {/* Stops */}
              <div className="mb-6">
                <ul className="space-y-1 text-sm text-[#374151]">
                  {line.stops.map((stop, index) => (
                    <li key={index} className="flex items-center">
                      <span className="mr-2">•</span>
                      <span>{stop}</span>
                    </li>
                  ))}
                </ul>
              </div>

              {/* Button */}
              <button className="w-full bg-[#1e40af] text-white py-2 rounded font-medium hover:bg-[#1e3a8a] transition-colors">
                Sélectionner
              </button>
            </button>
          ))}
        </div>
      </div>
    </div>
  )
}
